<template>
  <div class="order">
    <div class="topBgcolor"></div>

    <!-- 导航条 -->
    <van-nav-bar
      title="我的订单"
      left-text="返回"
      left-arrow
      :fixed='true'
      @click-left="back"
    />

    <!-- 头部背景图 -->
    <div class="my-bg">
      <div class="bg auto-img">
      </div>
    </div>

    <!-- 标签栏 -->
    <div class="tab-box">
      <div>
        <van-tabs
          v-model="tabIndex"
          color="#0b34ba"
          line-height="1px"
          :swipeable="true"
          :animated="true"
          title-active-color="#0b34ba"
          @change="toggleStatus"
        >
          <van-tab
            :title="v.title"
            v-for="(v,i) in tabItems"
            :key="i"
          >

            <div class="tab-item-bg" v-if="orderDatas.length > 0">
              <div
                class="tab-item"
                v-for="(item,index) in orderDatas"
                :key="index"
              >
                <div class="clearfix tab-item-box">
                  <div class="fl no">{{item.orderId}}</div>
                  <div class="fr text">
                    <div
                      v-if="item.status == 1"
                      @click="confirmReceive(item, index)"
                    >确认收货</div>
                    <div
                      class="text-box"
                      v-else
                    >
                      <div class="finished">已完成</div>
                      <div
                        class="remove"
                        @click="removeOrder(item.orderId,index)"
                      >
                        <van-icon
                          name="delete"
                          size="20"
                        />
                      </div>
                    </div>
                  </div>
                </div>

                <!-- 列表 -->
                <div class="order-box">

                  <!-- 订单信息 -->
                  <div>
                    <div
                      class="clearfix cell-box"
                      v-for="(value,i) in item.products"
                      :key="i"
                    >
                      <div class="fl pro-img">
                        <img
                          class="auto-img"
                          :src="value.smallImg"
                          alt=""
                        >
                      </div>
                      <div class="fl pro-info">
                        <div class="text-box-tab">
                          <div class="clearfix text-box-item">
                            <div class="fl text-name">{{value.name}}</div>
                            <div class="fl text-rule">{{value.rule}}</div>
                          </div>
                          <div class="text-enname">{{value.enname}}</div>
                        </div>
                        <div class="price-box">
                          <div class="fl price">￥{{value.price}}</div>
                          <div class="fr count">x{{value.count}}</div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div class="pro-box">
                    <div class="pro-boxing">
                      <div class="data">{{item.date | formatDate('yyyy-MM-dd hh:mm:ss')}}</div>
                      <div class="clearfix">
                        <div class="fl pro-count">共计 {{item.count}} 件商品</div>
                        <div class="fr pro-total">合计 ￥{{item.total}}</div>
                      </div>

                    </div>
                    <div class="left-box left"></div>
                    <div class="left-box right"></div>
                  </div>
                </div>

              </div>
            </div>

            <!-- 无商品的列表背景 -->
            <div
              class="main-box-null"
              v-else
            >
              <van-empty
                class="custom-image"
                image="https://img.yzcdn.cn/vant/custom-empty-image.png"
                description="你还没买任何东西，快去选购吧"
              >
                <van-button
                  round
                  color="#0b34ba"
                  class="bottom-button"
                  @click="goMenu"
                >
                  马上去逛逛
                </van-button>
              </van-empty>
            </div>

          </van-tab>
        </van-tabs>
      </div>
    </div>

  </div>
</template>

<script>
import "../assets/less/order.css";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      n: "2020-08-13T02:18:50.000Z",
      tabIndex: 0,
      orderDatas: [],
      tabItems: [
        { title: "全部订单" },
        { title: "待收货" },
        { title: "已完成" },
      ],
    };
  },

  created() {
    let tokenString = localStorage.getItem("CSTK");
    if (!tokenString) {
      this.$toast("请先登录");
      return this.$router.replace({ name: "Login" });
    }
    this.getOrderData(0);
  },

  methods: {
    // 跳转上一步
    back() {
      this.$router.go(-1);
    },

    // 回到菜单页面
    goMenu() {
      this.$router.push({ name: "Menu" });
    },

    // 获取订单数据
    getOrderData(status) {
      //  status 订单状态
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.replace({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
          status,
        },
      })
        .then((result) => {
          this.$toast.clear();
          this.orderDatas = [];
          //   

          // 数据变形
          //   {
          //       订单号:{
          //           data:'',
          //           count:0,
          //           total:0,
          //           status:0,
          //           商品数据:[{},{}]
          //       }
          //   }

          if (result.data.code == 70000) {
            let orderData = {};

            result.data.result.map((v) => {
              if (!orderData[v.oid]) {
                //   如果不存在该订单数据，则初始化一个对象
                orderData[v.oid] = {
                  orderId: v.oid,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price,
                  status: v.status,
                  products: [v],
                };
              } else {
                orderData[v.oid].count += v.count;
                orderData[v.oid].total += v.count * v.price;
                orderData[v.oid].products.push(v);
              }
            });
            // 
            for (let key in orderData) {
              this.orderDatas.push(orderData[key]);
            }

            // es6降序排序
            // 如果 b-a > 0  则 b 在 a 前面
            this.orderDatas.sort((a, b) => {
              return new Date(b.date).getTime() - new Date(a.date).getTime();
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 切换订单状态
    toggleStatus(name) {
      this.getOrderData(name);
    },

    // 确认收货
    confirmReceive(item, index) {
      //  item.orderId  订单号
      //   item.status  订单状态
      //  index   下标

      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oid: item.orderId,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/receive",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 80000) {
            //   this.$toast(result.data.msg)

            if (this.tabIndex == 1) {
              this.$dialog.confirm({ message: "是否确认收货？" }).then(() => {
                //   待收货
                setTimeout(() => {
                  this.orderDatas.splice(index, 1);
                  this.$toast(result.data.msg);
                }, 2000);
              });
            } else {
              item.status = 2;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 删除订单
    removeOrder(oid, index) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$dialog
        .confirm({
          title: "是否确认删除该订单？",
          confirmButtonColor: "#0c34ba",
        })
        .then(() => {
          // 参数序列化
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
            oid,
          });

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          this.axios({
            method: "POST",
            url: "/removeOrder",
            // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
            data,
          })
            .then((result) => {
              this.$toast.clear();
              

              if (result.data.code == 90000) {
                this.orderDatas.splice(index, 1);
                this.$toast(result.data.msg);
              }
            })
            .catch((err) => {
              this.$toast.clear();
              
            });
        });
    },
  },
};
</script>